<template><section><h1>input 输入框</h1>
<hr>
<p>通过鼠标或键盘输入内容，是最基础的表单域的包装。</p>
<h2>何时使用</h2>
<p>需要用户输入内容时</p>
<h3>基础输入框</h3>
<p>注: type, maxlength, minlength placeholder, 还有其他input 自带的属性可以直接写, 宽度默认为上层容器的100%, 展示例子里面做了限制为200px</p>
<demo-box>
                    <div slot="demo"><html><head></head><body><cat-input v-model="value1" placeholder="普通文字输入框" type="text"></cat-input>
<p>{{ value1 }}</p>

<cat-input v-model="value2" placeholder="密码输入框" type="password"></cat-input>

<cat-input v-model="value3" placeholder="disabled" type="text" disabled></cat-input>

<cat-input v-model="value3" placeholder="readonly" type="text" readonly></cat-input>

<cat-input v-model="value4" placeholder="限制最多输入5个" type="text" maxlength="5"></cat-input>
<p>{{ value4 }}</p>
</body></html></div>
                    <div slot="source-code"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">cat-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value1"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"普通文字输入框"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{ value1 }}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">cat-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value2"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"密码输入框"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"password"</span> &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">cat-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value3"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"disabled"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">cat-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value3"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"readonly"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">readonly</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">cat-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value4"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"限制最多输入5个"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">maxlength</span>=<span class="hljs-string">"5"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{ value4 }}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
</code></pre>
</div></demo-box><h3>三种大小</h3>
<p>注: 高度分别为 large  32px、 normal 28px 和 small 22px, 默认为normal</p>
<demo-box>
                    <div slot="demo"><html><head></head><body><cat-input v-model="value5" placeholder="小" size="small"></cat-input>
<cat-input v-model="value5" placeholder="中"></cat-input>
<cat-input v-model="value5" placeholder="大" size="large"></cat-input>
</body></html></div>
                    <div slot="source-code"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">cat-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value5"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"小"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value5"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"中"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value5"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"大"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"large"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input</span>&gt;</span>
</code></pre>
</div></demo-box><h3>带有图标的输入框</h3>
<p>注: icon的type 可以参照icon组件, onIconClick为图标事件触发函数, 默认图标是在右边, 可通过direction来控制方向</p>
<demo-box>
                    <div slot="demo"><html><head></head><body><cat-input v-model="value6" placeholder="搜索框" icon="search"></cat-input>

<cat-input v-model="value7" placeholder="我有按钮清除哦" :icon="icon7" :on-icon-click="clear7"></cat-input>

<cat-input v-model="value8" placeholder="搜索框" icon="search" direction="left"></cat-input>
</body></html></div>
                    <div slot="source-code"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">cat-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value6"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"搜索框"</span>  <span class="hljs-attr">icon</span>=<span class="hljs-string">"search"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">cat-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value7"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"我有按钮清除哦"</span>  <span class="hljs-attr">:icon</span>=<span class="hljs-string">"icon7"</span> <span class="hljs-attr">:on-icon-click</span>=<span class="hljs-string">"clear7"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">cat-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value8"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"搜索框"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"search"</span> <span class="hljs-attr">direction</span>=<span class="hljs-string">"left"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input</span>&gt;</span>
</code></pre>
</div></demo-box><h3>多行文本输入框</h3>
<p>注: 用于多行输入</p>
<demo-box>
                    <div slot="demo"><html><head></head><body><cat-input v-model="value9" placeholder="" type="textarea"></cat-input>
</body></html></div>
                    <div slot="source-code"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">cat-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value9"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">""</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"textarea"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input</span>&gt;</span>
</code></pre>
</div></demo-box><h3>带有状态的输入框</h3>
<p>注: 添加属性 status, 表示不同含义的输入框，只是颜色上的变化，不影响逻辑。提供四种状态：success，error，warning，info</p>
<demo-box>
                    <div slot="demo"><html><head></head><body><cat-input v-model="value10" placeholder="成功" status="success" icon="smile-o"></cat-input>

<cat-input v-model="value10" placeholder="错误" status="error" icon="meh-o"></cat-input>

<cat-input v-model="value10" placeholder="警告" status="warning" icon="meh-o"></cat-input>

<cat-input v-model="value10" placeholder="信息" status="info" icon="meh-o"></cat-input>
</body></html></div>
                    <div slot="source-code"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">cat-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value10"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"成功"</span>  <span class="hljs-attr">status</span>=<span class="hljs-string">"success"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"smile-o"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">cat-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value10"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"错误"</span>  <span class="hljs-attr">status</span>=<span class="hljs-string">"error"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"meh-o"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">cat-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value10"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"警告"</span>  <span class="hljs-attr">status</span>=<span class="hljs-string">"warning"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"meh-o"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">cat-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value10"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"信息"</span>  <span class="hljs-attr">status</span>=<span class="hljs-string">"info"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"meh-o"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input</span>&gt;</span>
</code></pre>
</div></demo-box><h2>Input 参数</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>输入框类型，原生属性</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>name</td>
<td>原生属性</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>icon</td>
<td>插入图标类型</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>direction</td>
<td>插入图标位置</td>
<td>String</td>
<td>left, right</td>
<td>right</td>
</tr>
<tr>
<td>on-icon-click</td>
<td>点击 Input 内的图标的钩子函数</td>
<td>function</td>
<td>—	—</td>
<td></td>
</tr>
<tr>
<td>value</td>
<td>绑定的值</td>
<td>String / Number</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>placeholder</td>
<td>占位文本（原生属性）</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用（原生属性）</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>readonly</td>
<td>是否只读（原生属性）</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>maxlength</td>
<td>最大长度（原生属性）</td>
<td>Number</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>minlength</td>
<td>最小长度（原生属性）</td>
<td>Number</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>max</td>
<td>最大值（原生属性）</td>
<td>Number</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>min</td>
<td>最小值（原生属性）</td>
<td>Number</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>autofocus</td>
<td>是否自动获取焦点（原生属性）</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>size</td>
<td>输入框尺寸</td>
<td>String</td>
<td><code v-pre="">small</code>, <code v-pre="">normal</code>, <code v-pre="">large</code></td>
<td>normal</td>
</tr>
<tr>
<td>status</td>
<td>输入框类型</td>
<td>String</td>
<td>success, error, warning, info</td>
<td>-</td>
</tr>
</tbody>
</table>
<h2>Input 事件</h2>
<p>注: 通过@focus 传入事件即可调用</p>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>focus</td>
<td>获得焦点时触发</td>
<td>event</td>
</tr>
<tr>
<td>blur</td>
<td>失去焦点时触发</td>
<td>event</td>
</tr>
<tr>
<td>change</td>
<td>在 Input 值改变时触发</td>
<td>event</td>
</tr>
<tr>
<td>enter</td>
<td>在 Input 按下enter触发</td>
<td>event</td>
</tr>
</tbody>
</table>


</section></template>
<style lang="scss" scoped>
  .cat-input {
    display: block;
    width: 200px;
    margin: 10px 0;
  }

  p {
    line-height: 35px;
  }
</style>
<script>
export default {
  data() {
    return {
      value1: '',
      value2: '',
      value3: '',
      value4: '',
      value5: '',
      value6: '',
      value7: '我有按钮清除哦',
      icon7: 'close',
      value8: '',
      value9: '',
      value10: ''
    }
  },
  watch: {
    value7 () {
      this.icon7 = this.value7 ? 'close' : ''
    }
  },
  methods: {
    clear7 () {
      this.value7 = ''
    }
  }
}
</script>


